$light-100: #fff;
$light-50: #c4c4c4;
$light-25: rgba(#ffffff, 0.25);
$dark-100: #000;
$dark-50: rgba(#000000, 0.5);
$dark-25: rgba(#000000, 0.25);
$error-box-color: #fdebeb;

$body-bg: #000;
$body-color: $light-100;
$primary: #d7198b;
$secondary: #377dff;
$accept: #3cc13b;
$danger: #ff000f;

$progress-bar-bg: $secondary;
$nav-tabs-link-active-color: $primary;
$nav-tabs-border-color: $light-50;

$default-bg-color: $light-25;
$secondary-bg-color: $light-50;
$font-transparent: $light-50;
$talent-bg-color: #262626;
$form-bg-color: rgba(196, 196, 196, 0.1);
$font-color2: $light-50;
$input-font-color: $light-100;

$theme-colors: (
	"primary": $primary,
	"secondary": $secondary,
	"succes": $accept,
	"danger": $danger,
	"light": $light-100,
	"gray": $talent-bg-color,
);
