.site-footer {
	border-top: 1px solid $light-25;
	.title,
	.description,
	a {
		font-size: 12px;
		line-height: 24px;
	}
	.title {
		font-weight: bold;
	}
	.description {
		font-weight: 500;
	}
	a {
		color: white;
		font-weight: 400;
	}
}

.footer-menu {
	li {
		margin: 10px 0;
		a {
			font-size: 12px;
			line-height: 21px;
			font-weight: 400;
			color: $light-50;
		}
	}
}

.copyright {
	font-size: 16px;
	font-weight: 700;
	.social {
		> * {
			margin: 0 6%;
			@include media-breakpoint-up(md) {
				margin: 0 10px;
			}
		}
		a {
			display: inline-block;
			width: 30px;
			font-size: 16px;
			&:hover {
				text-decoration: none;
			}
		}
	}
}
